import React, { Component } from "react"
import { importAllFiles } from "../helpers/functions"
import { graphql, useStaticQuery } from "gatsby"
import GalleryTemplate from "../templates/GalleryTemplate"

const Laser = () => {
  // state = {
  //   smallPhotos: importAllFiles(
  //     require.context(
  //       "../assets/photos/transparent_laser_small",
  //       false,
  //       /\.(png|jpe?g|svg)$/
  //     )
  //   ),
  // }

  const data = useStaticQuery(graphql`
    {
      allFile(
        filter: { extension: { eq: "png" }, sourceInstanceName: { eq: "data" } }
      ) {
        edges {
          node {
            relativePath
            publicURL
            absolutePath
          }
        }
      }
    }
  `)
  console.log(data)

  return <GalleryTemplate pageName="Laser" photos={data} />
}

export default Laser

